@font-face {
    font-family: Harriet;
    src: url(HarrietDisplay-Black.ttf);
}

.contact {
    overflow-x: hidden;
    font-size: 60px;
    position: relative;
    top: 135px;
    z-index: 100;
    padding: 50px 0;
    align-self: center;
    text-align: center;
    font-family: Harriet, serif;
    color: #f2f2f2;
}

.contact:before,
.contact:after {
 background-color: #98d9ff;
 content: "";
 display: inline-block;
 height: 5px;
 position: relative;
 vertical-align: middle;
 width: 25%;
}
.contact:before {
 right: 0.5em;
 margin-left: -50%;
}
.contact:after {
 left: 0.5em;
 margin-right: -50%;
}

.contact-background {
    background-color: black;
}

.contact-form {
    position: relative;
    top: 130px;
    width: 50%;
    font-family: Harriet, serif;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin:0 auto;
    color: #f2f2f2;
}

label {
    padding: 5px 0;
    font-family: Balto-Med, sans-serif;
    font-size: 15px;
}

input {
    width: 100%;
    font-family: Balto-Med, sans-serif;
    margin: 5px 0;
    background-color: black;
    outline: none;
    border: none;
    color: #f2f2f2;
    padding: 10px 0;
    border-bottom: 1px solid #f2f2f2;
}


textarea {
    background-color: black;
    width: 100%;
    margin: 15px 0;
    outline: none;
    border: none;
    color: #f2f2f2;
    font-family: Balto-Med, sans-serif;
    padding: 10px 0;
    border-bottom: 1px solid #f2f2f2;
    resize: none;
}

button {
    margin: 20px 0;
    padding: 20px;
    font-family: Balto-Med, sans-serif;
    color: #f2f2f2;
    border: none;
    cursor: pointer;
    background-color: black;
    border: 1px solid #f2f2f2;
    transition: 200ms ease-in-out all;
    width: 100%;
    outline: none;
}

button:hover {
    background-color: #0a0a0a;
}

p {
    color: #e74c3c;
    font-size: 12px;
    margin: 2px 0;
    font-family: Balto-Med, sans-serif;
}

.sent {
    color: #2ecc71;
}

.socials {
    display: flex;
    justify-content: space-evenly;
    padding-bottom: 20px;
}

.fa {
    font-size: 25px;
    color: #f2f2f2;
    padding: 0;
    background-position: 0% 0%;
    background-size: 0% 0px;
}

.contact-loader {
    display: flex;
    color: #f2f2f2;
    align-items: center;
    justify-content: center;
    font-size: 15px;
    animation: example 1.2s ease-in-out infinite;
}

.pulse {
    height: 15px
}

@keyframes example {
    0%   {transform: rotate(0deg);}
    100%   {transform: rotate(360deg);}
    } 


    @media screen and (max-width: 759px) {
        .fa {
            font-size: 20px;
        }

        button {
            padding: 10px;
        }

        .contact-form {
            width: 90%;
            top : 75px;
            bottom: 75px;
            position: relative;
        }

        .contact {
            top: 15px;
            font-size: 45px;
            width: 90%;
            margin: 0 auto;
            position: relative;
        }
    }
    
