@import url('https://fonts.googleapis.com/css?family=Inconsolata&display=swap');

.error {
    display: flex;
    align-items: center;
    font-family: 'Courier', serif;
    height: 100vh;
    background-color: black;
    justify-content: center;
    font-size: 50px;
    text-align: center;
}

.cursor {
    width: 25px;
    background-color: #f2f2f2;
    height: 10px;
    margin: 5px;
    margin-top: 35px;
    animation: blink 1.2s step-start 0s infinite;
}

.backHome {
    text-decoration: none;
    font-family: Balto-Med, sans-serif;
    color: #f2f2f2;
    background-image: linear-gradient(#98d9ff, #98d9ff);
    background-position: 0% 100%;
    background-repeat: no-repeat;
    background-size: 0% 30px;
    padding: 5px;
    transition: all .3s;
    font-size: 20px;
  }
  
  .backHome:hover, .backHome:focus {
    background-size: 100% 30px;
    color: black;
  }

  .backHomeBtn {
        position: relative;
    margin: 0 auto;
    text-align: center;
    display: flex;
    position: relative;
    bottom: 325px;
    justify-content: center;
  }

@keyframes blink {
    50% {
      opacity: 0.0;
    }
  }

.not-found {
    font-size: 35px;
    color: #f2f2f2;
}

@media screen and (max-width: 759px) {
  .path {
    display: none;
  }
}