@font-face {
    font-family: Harriet;
    src: url(HarrietDisplay-Black.ttf);
    font-family: Balto;
    src: url(Balto-Black.ttf);
    font-family: Balto-Med;
    src: url(Balto-Medium.ttf)
}

.projects-background {
    width: 100%;
    height: 100%;
}

.projects li {
    border: none;
    font-size: 30px;
    font-family: Harriet, serif;
    margin: 0;
    padding: 0 20px;
    padding-top: 20px;
    font-weight: bold;
    list-style: none;
    z-index: 100;
}

.project-title {
    position: relative;
    font-size: 60px;
    margin: 0;
    z-index: 100;
    padding-top: 185px;
    margin-bottom: 50px;
    font-family: Harriet, serif;
    text-align: center;
    font-weight: lighter;
    color: #f2f2f2;
}

.project-title:before,
.project-title:after {
 background-color: #98d9ff;
 content: "";
 display: inline-block;
 height: 5px;
 position: relative;
 vertical-align: middle;
 width: 25%;
}
.project-title:before {
 right: 0.5em;
 margin-left: -50%;
}
.project-title:after {
 left: 0.5em;
 margin-right: -50%;
}

.project-loader {
    position: relative;
    top: 100px;
    animation: fadein 1s;
}

@keyframes fadein {
    0% { opacity: 0; }
    99% { opacity: 0; }
    100%   { opacity: 1; }
}

.projects p {
    color: black;
    font-size: 20px;
    margin: 20px 0;
    font-family: Balto-Med, sans-serif;
    padding: 0 20px;
}

.projects .date {
    opacity: 0.8;
    margin: 20px 0;
    align-items: center;
    font-size: 10px;
}

.projects {
    margin-bottom: 10px;
    /* width: 50%; */
    background-color: #f2f2f2;
    /* border-top: 5px solid #98d9ff; */
}

.projects-container {
    display: grid;
    grid-template-columns:33% 33% 33%;
    grid-gap: 2%;
    flex-direction: row;
    padding: 75px;
    justify-content: space-evenly;
    font-family: Balto, sans-serif;
}

.project-button {
    color: black;
    background: none;
    display: inline-block;
    font-size: 15px;
    margin: 20px;
    font-weight: bold;
    text-transform: capitalize;
    background-size: 0;
    text-decoration: none;
    background-color: #f2f2f2;
    transition: 200ms ease-in-out all;
}

