img {
    width: 100%;
    height: auto;
    filter: grayscale(50%);
    transition: 500ms ease all;
    z-index: -100;
}

/* .img-container:hover img {
    filter: none;
    transform: scale(1.1);
} */

.projects-container a {
    padding: 5px;
    background-image: linear-gradient(#98d9ff, #98d9ff);
    background-position: 0% 100%;
    background-repeat: no-repeat;
    background-size: 0% 30px;
    transition: background-size .3s;
}

.projects-container a:hover, .projects-container a:focus {
    background-size: 100% 30px;
}

.readMore {
    display: block;
    width: inherit;
    padding: 0 20px;
    text-align: left;
    margin: 0;
    font-family: Balto, sans-serif;
    color: black;
    background-color: #f2f2f2;
}

.readMore:hover, .readMore:focus {
    background-color: #f2f2f2;
}


@media screen and (max-width: 759px){
	
	/* .projects {
		width: 90%;
    }
     */
    .projects-container {
        grid-template-columns:90%;
        padding: 0;
        bottom: 40px;
        position: relative;

    }

    .project-title {
        padding-top: 140px;
        font-size: 45px;
        width: 60%;
        margin: 0 auto;
        bottom: 75px;
        position: relative;
    }

}