.about {
    display: flex;
    align-items: center;
    flex-direction: column;
}


.about p {
    color: #f2f2f2;
    font-family: Balto-Med, sans-serif;
    margin-top: 0px;
    line-height: 50px;
    font-size: 20px;
    border-bottom: 0.5px solid #f2f2f2;
}

.about-button {
    text-decoration: none;
    font-family: Balto-Med, sans-serif;
    color: #f2f2f2;
    background-image: linear-gradient(#98d9ff, #98d9ff);
    background-position: 0% 100%;
    background-repeat: no-repeat;
    background-size: 0% 30px;
    padding: 5px;
    transition: all .3s;
    font-size: 20px;
  }
  
  .about-button:hover, .about-button:focus {
    background-size: 100% 30px;
    color: black;
  }

  .skills-title {
    display: flex;
    width: 50%;
    margin: 0 auto;
    text-align: center;
    justify-content: space-evenly;
    color: #f2f2f2;
  }

  .app, .tech {
    align-items: center;
    font-size: 20px;
    cursor: pointer;
    font-family: Balto-Med, sans-serif;
    color: #f2f2f2;
    background-image: linear-gradient(#98d9ff, #98d9ff);
    background-position: 0% 100%;
    background-repeat: no-repeat;
    background-size: 0% 30px;
    transition: all .3s;
    padding: 5px;
    white-space: nowrap;
  }

.app:hover, .tech:hover, .app:focus, .tech:focus {
    background-size: 100% 30px;
    color: black;
  }

.skills {
    font-family: Balto-Med, sans-serif;
    height: 200px;
    padding: 20px;
    font-size: 20px;
    color: #f2f2f2;
    line-height: 30px;
    display: flex;
    justify-content: center;
    list-style: none;
}

.skills ul {
    /*display: grid;
    grid-template-columns: 2fr 2fr;*/
    list-style: none;
    padding-left: 0;
    display: flex;
    justify-content: space-evenly;
}

.skills li {
    padding: 0 10px;
    text-align: left;
}

i {
    padding: 0 15px;
}

  .show {
    display: block;
    animation: fadeIn 600ms ease-in;
  }

  .remove {
    display: none;
  }

@keyframes fadeIn {
  0%   { opacity: 0; }
  100% { opacity: 1; }
}

@keyframes fadeOut {
  0%   { opacity: 1; }
  100% { opacity: 0; }
}


@media screen and (max-width: 759px) {
    .skills-container {
        flex-direction: column;
    }

    .about-container {
        bottom: 75px;
        position: relative;
    }

    .about-title {
        padding-top: 0px;
        font-size: 45px;
    }

    .about p {
        font-size: 15px;
        line-height: 1.9;
        width: 80%;
    }

    .about-container p:first-letter {
        font-size: 56px;
    }

    .about-button {
        font-size: 15px;
    }

    .app, .tech {
        font-size: 12px;
        white-space: nowrap;
    }

    .skills {
        font-size: 15px;
        position: relative;
        bottom: 75px;
    }

    .skills-title {
        margin: 0 auto;
        position: relative;
        bottom: 75px;
    }
}

