body {
  font-family: 'ButlerRegular', serif;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  background-color: rgb(0, 0, 0);
  overflow-x: hidden;
}

/* width */
body::-webkit-scrollbar {
  width: 5px;
}

/* Track */
body::-webkit-scrollbar-track {
  background: #000000;
}

/* Handle */
body::-webkit-scrollbar-thumb {
  background: #f2f2f2;
}

/* Handle on hover */
body::-webkit-scrollbar-thumb:hover {
  background: #f2f2f2;
}





@font-face {
  font-family: Harriet;
  src: url(/static/media/HarrietDisplay-Black.7a3a4b15.ttf);
}

.container {
    overflow-x: hidden;
    position: relative;
    color: #f2f2f2;
    display: flex;
    min-height: 100vh;
    font-family: Harriet, serif;
    align-items: center;
    flex-direction: column;
    justify-content: center;

}

h1 {
  font-size: 80px;
  margin: 0;
  z-index: 100;
}

.list {
  display: flex;
  margin: 0;
  position: relative;
  width: 250px;
  letter-spacing: 1px;
  bottom: 18px;
  font-family: Balto-Med, sans-serif;
  -webkit-transform: skewX(-30deg);
          transform: skewX(-30deg);
  flex-direction: column;
  left: 100px;
  z-index: 100;
}

.background {
  position: absolute;
  z-index: -100;
  width: 100%;
  height: 100%;
  background-color: black
}

.site-title {
  font-size: 2rem;
}

.view {
  text-decoration: none;
  color: #f2f2f2;
  padding: 5px;
  /* background-image: linear-gradient(rgb(255, 238, 0), rgb(255, 238, 0)); */
  background-image: linear-gradient(#98d9ff, #98d9ff);
  background-position: 0% 100%;
  background-repeat: no-repeat;
  background-size: 0% 30px;
  transition: all .3s;
}

.view:hover, .view:focus {
  background-size: 100% 30px;
  color: black;
}

@media screen and (max-width: 759px){
  
  h1 {
    font-size: 80px;
  }

  .list {
    font-size: 15px;
    left: 100px;
    bottom: 18px;
  }

}
 
@font-face {
    font-family: Harriet;
