@font-face {
    font-family: Harriet;
    src: url(HarrietDisplay-Black.ttf);
}

.navHolder {
    font-family: Harriet, serif;
    position: fixed;
    top: 0px;
    font-size: 18px;
    padding-left: 0;
    display: flex;
    padding-right: 0;
    width: 100%;
    z-index: 1000;
    pointer-events: none;
}

.burger {
    pointer-events: all;
    display: none;
    font-size: 20px;
    width: 1%;
    position: fixed;
}

.navHolder ul {
    pointer-events: all;
    display: flex;
    align-items: center;
    list-style: none;
    justify-content: center;
    background-color: rgba(0, 0, 0, 1);
    padding: 0;
    float: right;
    padding: 12px;
    width: 100%;
    margin: 0;
    position: relative;
}

.nav a {
    z-index: 2001;
    text-decoration: none;
    margin: 0 45px;
    padding: 0 5px;
    color: #f2f2f2;
    text-transform: capitalize;
    text-decoration: none;
    /* background-image: linear-gradient(rgb(255, 238, 0), rgb(255, 238, 0)); */
    background-image: linear-gradient(#98d9ff, #98d9ff);
    background-position: 0% 100%;
    background-repeat: no-repeat;
    background-size: 0% 25px;
    transition: all .3s;
}

.nav a:hover, .nav a:focus {
  background-size: 100% 30px;
  color: black;
}

.sub-name{
    font-family: Harriet, serif;
    color: #f2f2f2;
    margin: 0;
    display: flex;
    color: #f2f2f2;
    width: 100%;
    color: inherit;
    float: right;
}

.sub-name a {
    margin: 0;
}

.sub-name a:hover, .sub-name a:focus{
    background-size: 0% 0px;
    color: #f2f2f2;
}

.active {
  bottom: 0;
}

@media screen and (max-width: 759px) {
    .navHolder ul {
        padding-bottom: 37px;
        align-items: stretch;
    }


    ul {
        flex-direction: column;
        text-align: center;
        bottom: 160px;
        transition: 300ms ease-in-out bottom;
        width: 100%;
        padding-bottom: 37px;
    }

    li {
        padding: 10px;
    }

    .burger {
        top: 14px;
        display: block;
        cursor: pointer;
    }

    .sub-name {
        display: none;
    }
}





